import wPicker from '../../components/w-picker/w-picker.vue';
var _this;
export default {
	components:{
		wPicker,
	},
	data() {
		return {
			projectId:"",
			width:"",        ///设置输入框的长度
			content:"",      //故事的内容
		}
	},
	onLoad(){
		
	},
	onShow(){
		_this = this;
		_this.getSystem();
		uni.getStorage({
			key:'nowInProject',
			success:(res)=>{
				_this.projectId = res.data.projectId;
			}
		})
	},
	methods: {
		
		//获取系统的信息  
		getSystem:function(){
			_this = this;
			uni.getSystemInfo({
				success:(res)=>{
					_this.width = res.windowWidth-parseInt(res.windowWidth*0.2);
				}
			})
		},
		
		
		openPicker:function(){
			this.$refs.picker1.show();
		},
		
		
		onConfirm:function(e){
			console.log(e);
			let chooseTime = e.result;
			_this.chooseTime = chooseTime;
		},
		
		
		
		
		//提交增加用户故事
		submit:function(){           //进行再次的确认提交
			_this = this;
			if(_this.content&&_this.storyTime){
				uni.showLoading({
					success:()=>{
						
					}
				})
			}else{
				uni.showToast({
					title:"请完善信息",
					duration:1000,
					icon:"none"
				})
			}
		}
		
	}
}
